.footer__copyright {
  margin: 0;
  padding-bottom: 60px;
  font-size: 18px;
  font-weight: 400;
  line-height: calc(22/18);
  color: #545454;
}

@media screen and (max-width: 900px) {
  .footer__copyright {
    margin-left: 19px;
  }
}

@media screen and (max-width: 390px) {
  .footer__copyright {
    font-size: 14px;
    line-height: calc(17/14);
  }
}
